/* helpers */
/* base styles */
@font-face {
  font-family: Roboto;
  src: url(301fe70f8f0f41c236317504ec05f820.ttf);
}
@font-face {
  font-family: PT Sans;
  src: url(4ea26cd5e7f64894d6c2451446f7dda5.ttf);
}
/* common styles */
body {
  background: #2a3f50;
  color: #fff;
  font-family: PT Sans;
}
.header,
.main {
  width: 100%;
}
.header {
  position: block;
  top: 0;
  z-index: 100;
  background-color: #2a3f50;
}
.menu-header,
.menu-header__logo-part {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.container {
  padding: 0 24px;
}
.menu-second {
  visibility: hidden;
}
.logo {
  font-size: 1.3em;
  padding-right: 0.625em;
  font-family: "Roboto", sans-serif;
  font-weight: bolder;
  line-height: 1.75em;
  letter-spacing: 0.7px;
}
.menu-icons {
  padding-left: 2.5em;
}
.navigation {
  display: flex;
  justify-content: flex-end;
}
.navigation a {
  color: #8da5b8;
  font-family: "PT Sans", sans-serif;
  font-size: 1.375em;
  line-height: 1.3em;
  font-weight: 400;
  padding-left: 1em;
  text-decoration: none;
}
.navigation .done-tasks:hover {
  color: #82c7e0;
}
.navigation span {
  padding-left: 1em;
}
.navigation2 {
  display: flex;
  justify-content: center;
  padding-top: 2.5em;
}
.navigation2 a {
  color: #8da5b8;
  font-family: "PT Sans", sans-serif;
  font-size: 1.375em;
  line-height: 1.3em;
  font-weight: 400;
  padding-left: 1em;
  text-decoration: none;
}
.navigation2 .done-tasks:hover {
  color: #82c7e0;
}
.navigation2 span {
  padding-left: 1em;
}
.navigation:active + .list .box .list__box .box-info h3 {
  text-decoration: line-through;
}
.main__list .list {
  display: none;
}
@media screen and (min-width: 768px) {
  .list .box .list__box .box-info p {
    min-width: 396px;
  }
  .work-list .list .box .tomato__level {
    width: 5.5em;
  }
  .box-date {
    width: 15% !important;
  }
  .box-info {
    width: 75% !important;
  }
  .pomodoros-settings__points {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
