}
@media screen and (min-width: 1024px) {
  .box-date {
    width: 10%!important;
  }
  .box-info {
    width: 85%!important;
  }
  .list .box .list__box .box-info p {
    min-width: 604px;
  }
  .header-pomodoros {
    position: absolute;
    top: 7.4em;
    left: 33%;
    display: table;
    width: 35%;
    overflow: auto;
    margin: auto;
  }
  .main__global-list {
    margin-top: -7em;
  }
  .logo-settings {
    visibility: hidden;
  }
  .menu-second {
    visibility: inherit;
    display: flex;
    justify-content: center;
  }
  .menu-second .logo2 {
    font-size: 1.75em;
    font-family: "Roboto", sans-serif;
    font-weight: bolder;
    line-height: 2em;
