  .button-navigation .go-tasks-button {
    width: 151px;
    height: 2.75em;
    font-size: 1.1875em;
    line-height: 1.3125em;
  }
  .pomodoros-categories {
    padding-top: 7em!important;
  }
  .buttons-navigation {
    display: flex;
    justify-content: center;
  }
  .buttons-navigation button {
    font-size: 1.1875em;
    font-weight: 700;
    line-height: 1.3125em;
  }
  .buttons-navigation .go-tasks-button {
    width: 151px;
    height: 3em !important;
  }
  .buttons-navigation .save-tasks-button {
    width: 101px;
    height: 3em !important;
  }
  .pomodoros-settings {
    padding-top: 96px;
  }
  .pomodoros-settings__points {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-top: 6em;
  }
  .your-cycle {
    padding-top: 2em!important;
  }
  .buttons-navigation {
    padding-top: 4.5em;
    padding-bottom: 4.5em;
  }
