    display: flex;
    justify-content: center!important;
  }
  .buttons-navigation .go-tasks-button {
    height: 3em !important;
    margin-right: 24px;
  }
  .buttons-navigation .go-cancel {
    width: 110.3125em;
    height: 2.75em;
    font-size: 1.1875em;
  }
  .buttons-navigation .go-remove {
    width: 120.3125em;
    height: 2.75em;
    font-size: 1.1875em;
  }
  .navigation-start .start-task {
    width: 102px;
    height: 2.75em;
    font-size: 1.3em;
  }
  .navigation-timer button {
    height: 2.75em;
    width: 170.3125em;
    margin-left: 1.3em;
  }
  .modal-content {
    width: 70%!important;
  }
  .modal-content .modal-form {
    padding-bottom: 2.5em;
    padding-top: 6px;
  }
  .notification-block {
    width: 50%!important;
    margin-left: auto;
  }
