  background-color: #00d4d9;
}
.modal-form__category--radio-list .custom-radio + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
}
.modal-form__category--radio-list .custom-radio + label::before {
  content: "";
  display: inline-block;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #adb5bd;
  border-radius: 50%;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}
